import moveVerify from '@/components/helang-moveVerify/helang-moveVerify.vue';
import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	navBar,
	appid,
	appsecret
} = app;
export default {
	components: {
		moveVerify
	},
	data() {
		return {
			navBar: navBar,
			nickname: '',
			mobile: '',
			password: '',
			confirm_pwd: '',
			number_code: '',
			verify_code: '',
			showcode: false,
			back: true,
			display1:true,
			identifying:'',
			scrollHeight:'',
			resultData: {}
		};
	},
	methods: {
		leftClick() {
			
			uni.navigateTo({
				url:'login'
			})
			setTimeout(()=>{window.location.reload();},100)
		},
		pageUrl(data){
			uni.navigateTo({
				url:data
			})
		},
		// 滑动验证
		verifyResult(res) {
			// console.log(res);
			this.resultData = res;
			if (this.resultData.flag == true) {
				this.captcha()
				return;
			}
		},
		captcha() {
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
					sign: sign
				},
				obj
			);
			rs.getRequests('random', params, res => {
				let data = res.data;
				if (data.code == 200) {
					this.number_code = data.data.number;
					uni.setStorageSync("laravel_session", res.header["Set-Cookie"]);
				} else {
					rs.Toast(data.msg);
				}
			});
		},
		// 获取短信验证码
		getCode() {
			let timeStamp = Math.round(new Date().getTime() / 1000);
			if (!this.mobile) {
				rs.Toast('手机号不能为空');
				return;
			}
			if (!this.number_code) {
				rs.Toast('请先拖动滑块');
				return;
			}

			var reg = /^[\u4e00-\u9fa5_a-zA-Z0-9]+$/;
			if (!reg.test(this.mobile)) {
				uni.showToast({
					title: '不能输入特殊字符和空格',
					duration: 2000,
					icon: "none"
				});
				return;
			}

			let obj = {
				appid: appid,
				mobile: this.mobile,
				timeStamp: timeStamp
			};
			var that = this;
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var random_str = md5.hexMD5(appsecret + this.number_code);
			let params = Object.assign({
					sign: sign,
					secret_str: random_str,
					active: 2
				},
				obj
			);
			uni.request({
				url: app.rootUrl + "/mobileOrder/sendCode",
				method: 'POST',
				header: {
					'content-type': 'application/json', // 默认值
					'cookie': uni.getStorageSync("laravel_session")
				},
				data: params,
				success: function(res) {
					if (res.data.code == 200) {
						that.$refs.code.sendCode();
						that.number_code = res.data.data.random_str;
						rs.Toast('验证码已发送到你手机中，请注意查收');
					} else {
						that.verifyReset();
						rs.Toast(res.data.msg);
					}
				},
			})

		},
		verifyReset() {
			this.$refs.verifyElement.reset();
			/* 删除当前页面的数据 */
			this.resultData = {};
		},
		register() {
			
			if (!this.nickname) {
				rs.Toast('单位名称不能为空');
				return;
			}
			if (!this.mobile) {
				rs.Toast('手机号不能为空');
				return;
			}
			if (!this.password) {
				rs.Toast('密码不能为空');
				return;
			}
			var reg = /^[\u4e00-\u9fa5_a-zA-Z0-9]+$/;
			if (!reg.test(this.nickname) || !reg.test(this.mobile) || !reg.test(this.password)) {
				rs.Toast('不能输入特殊字符和空格');
				return;
			}
			if (this.password.length <6) {
				rs.Toast('请设置六位及以上的密码');
				return;
			}
			if (this.password != this.confirm_pwd) {
				rs.Toast('请确保密码一致');
				return;
			}
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp,
				nickname: this.nickname,
				mobile: this.mobile,
				password: this.password,
				confirm_pwd: this.confirm_pwd,
				verify_code: this.verify_code,
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			if (this.identifying) {
				obj.openid = this.identifying
			}
			let params = Object.assign({
				sign: sign,
			}, obj)
			var that = this;
			uni.request({
				url: app.rootUrl + "/mobileOrder/register",
				method: 'POST',
				data: params,
				header: {
					'content-type': 'application/json', // 默认值
					'cookie': uni.getStorageSync("laravel_session")
				},
				success: function(res) {
					if (res.data.code == 200) {
						rs.Toast('注册成功');
						setTimeout(() => {
							uni.navigateTo({
								url: './login',
							})
						}, 1000)
					} else {
						rs.Toast(res.data.msg);
						that.verifyReset()
					}
				}
			})
		}
	},

	onLoad(options) {
				var that = this;
				that.identifying = options.identifying;














			}
};
